<template>
  <v-container>
    <v-col cols="12" class="text-left orders__back" @click.prevent="goBack">Regresar</v-col>

    <h1 class="profile">Orden #{{id_order}}</h1>

    <v-col>
      <v-card>
        <v-col class="pa-1">
          <v-card-text class="py-1">{{deliveryType(orderData.delivery_type_id)}}</v-card-text>
          <v-card-text class="py-1">Fecha y Hora: {{orderData.created_at}}</v-card-text>
        </v-col>
      </v-card>
    </v-col>
    <h3 class="profile">Productos</h3>
    <v-col cols="12"  v-for="product in orderDetail" :key="product.id">
      <v-card style="display: flex">
        <v-col class="pa-1" cols="3">
          <v-img
            height="150"
            width="150"
            contain
            :src="product.image"
            :lazy-src="product.image"
            :alt="product.description"
            class="px-3 mx-auto"
          ></v-img>
        </v-col>
        <v-col class="pa-1 my-auto" cols="9">
          <v-card-text class="py-1">{{product.name}}</v-card-text>
          <v-card-text class="py-1">Cantidad: {{product.sale.quantity}}</v-card-text>
          <v-card-text class="py-1">Precio: {{product.sale.sale_price.toFixed(2)}}</v-card-text>
        </v-col>
      </v-card>
    </v-col>
    <v-col>
      <v-card class="mt-1">
        <v-col class="pa-1">
          <v-card-text class="py-1">Subtotal: ${{orderData.total.toFixed(2)}}</v-card-text>
          <v-card-text
            class="py-1"
          >Costo de envio: ${{orderData.delivery_price !== null ? orderData.delivery_price.toFixed(2) : shiptotal.toFixed(2) }}</v-card-text>
          <v-card-text class="py-1">Total: {{totalPrice}}</v-card-text>
        </v-col>
      </v-card>
    </v-col>

    <!-- <v-col cols="12" v-for="order in myOrders" :key="order.id">
