  </v-container>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      id_order: null,
      totalDetail: 0,
      shiptotal: 0
    };
  },
  async mounted() {
    await this.reloadData();
  },
  computed: {
    ...mapState("orders", ["orderDetail"]),
    ...mapGetters("orders", ["orderData"]),
    totalPrice() {
      this.totalDetail = 0;
      this.totalDetail = this.orderData.total + this.orderData.delivery_price;
      return this.totalDetail.toFixed(2);
    }
  },
  methods: {
    ...mapActions("orders", ["getOrderData"]),
    deliveryType(id) {
      if (id === 1) {
        return "Entrega a domicilio: " + this.orderData.address.address;
      } else {
        return "Recoge en tienda";
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    reloadData() {
      if (this.$route.params.id) {
        this.id_order = this.$route.params.id;
        this.getOrderData(this.$route.params.id);
      }
    }
  }
};
</script>
